import React, { useState } from "react";
import { useClient, useEffect } from "../../util/customhooks";
import "react-notifications/lib/notifications.css";
import { NotificationContainer, NotificationManager } from "react-notifications";
import { withErrorHandling } from "../../util/helper";
import ReactLoading from "react-loading";

interface Product {
	id: string;
	title: string;
	price: string;
}

const Products = () => {
	const [products, setProducts] = useState<Product[]>([]);
	const client = useClient();

	useEffect(async () => {
		withErrorHandling(
			async () => {
				const result = await client.fetch("/api/products.php");
				setProducts(result);
			},
			() => {
				NotificationManager.error("Authentication failed");
			}
		);
	}, []);

	return (
		<div className="container p-4">
			<NotificationContainer />
			<ReactLoading />
			<h1 className="mb-4 text-2xl font-semibold">Termkek</h1>
			{products.map((product) => {
				return <div key={product.id}>{product.title}</div>;
			})}
		</div>
	);
};

export default Products;
